import React from 'react';
import './App.css';
import TypesProducts from './components/TypesProducts/TypesProducts';
import Info from './components/Info/Info';
import Content from './components/Content/Content';
import {Route} from "react-router-dom";

const App = (props) => {

    // Footer пока не импортируется ( Не доделан )
    return (
        <section className="main">

            <Route  exact path='/'
                    render={() => <Content
                    ContentApps={props.state.ContentApps}/>}
                    />

            {/* Информация в отображаемая в зависимости от выброного каталога */}
            
            <Route  path='/PuttiesTypes'
                    render={() => <TypesProducts 
                    TypesProducts={props.state.TypesProducts} />}/>
                    

            <Route  path='/Universal putty'
                    render={() => <Info  
                    PuttiesInfo={props.state.PuttiesInfo.UniversalPutty} />}/>

            <Route  path='/Finely dispersed putty'
                    render={() => <Info 
                    PuttiesInfo={props.state.PuttiesInfo.UniversalPutty} />}/>

            <Route  path='/Putty with aluminum'
                    render={() => <Info 
                    PuttiesInfo={props.state.PuttiesInfo.UniversalPutty} />}/>

            <Route  path='/Glass fiber putty'
                    render={() => <Info 
                    PuttiesInfo={props.state.PuttiesInfo.UniversalPutty} />}/>

            <Route  path='/Carbon fiber putty'
                    render={() => <Info 
                    PuttiesInfo={props.state.PuttiesInfo.UniversalPutty} />}/>

            <Route  path='/Spray putty'
                    render={() => <Info 
                    PuttiesInfo={props.state.PuttiesInfo.UniversalPutty} />}/>
        </section>
    );
};


export default App;
